
import siteConfig from 'config/config'
import IconLink from 'components/IconLink'

export default Homepage =
  name: 'homepage'
  data: ->
    contactLinks: siteConfig.contactLinks

  mounted: ->
    @$router.app.$emit 'changeLayoutColors', 'accent'

  components:
    IconLink: IconLink




